import React from 'react';
import styled from 'styled-components';

function TextMobile() {
    return (
        <StyledDiv>
            <p>    .</p>
        </StyledDiv>
    );
}
const StyledDiv = styled.div`
    text-align: center;
    font-weight: bold;
    margin: 0 0 -10px;
    display: none;

    @media screen and (max-width: 767px) {
        display: block;
    }
`
export default TextMobile;